import React from 'react';
import styles from './Sidebar.module.css';
import { Link } from 'react-router-dom';

function Sidebar({ isOpened, navItems, onClose }) {
  return (
    <>
      <div onClick={onClose} className={`${styles.sidebarScreen} ${isOpened ? styles.open : ''}`}></div>
      <div className={`${styles.sidebar} ${isOpened ? styles.open : ''}`}>
        <div className={styles.sidebarContent}>
          <div className={styles.navLinks}>
            {navItems.map((item, index) => (
              <Link to={item.link} key={index}>
                <a
                  href="#"
                  className={`${styles.navLink} ${isOpened ? styles.animateIn : styles.animateOut}`}
                  style={{ animationDelay: `${index * 0.1}s` }}
                  onClick={onClose}
                >
                  {item.text}
                </a>
              </Link>
            ))}
          </div>
        </div>
      </div>
    </>
  );
}

export default Sidebar;
