import React, { useEffect, useState } from 'react';

const CallbackPage = () => {
  const [token, setToken] = useState(null);
  const [error, setError] = useState(null);

  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const code = urlParams.get('code');

    if (code) {
      // Cloudflare Workers code  access token 
      fetchAccessToken(code);
    }
  }, []);

  const fetchAccessToken = async code => {
    try {
      const response = await fetch(`https://authcationmiddle.urous3814.workers.dev/?code=${code}`);
      const data = await response.json();

      if (data.access_token) {
        setToken(data.access_token);
        //  GitHub API  
      } else {
        setError('Failed to retrieve access token');
      }
    } catch (err) {
      setError('Error occurred while fetching access token');
    }
  };

  return (
    <div>{token ? <p>Access Token: {token}</p> : error ? <p>Error: {error}</p> : <p>Fetching access token...</p>}</div>
  );
};

export default CallbackPage;
