import React from 'react';
import { Carousel } from 'react-responsive-carousel';
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import styles from './ImageSlider.module.css';

function ImageSlider() {
  const images = [
    { src: '/images/research1.jpg', alt: 'Research Image 1', caption: 'Innovative AI Techniques' },
    { src: '/images/research2.jpg', alt: 'Research Image 2', caption: 'Genomic Data Analysis' },
    { src: '/images/research3.jpg', alt: 'Research Image 3', caption: 'Bio-signal Processing' },
  ];

  return (
    <Carousel autoPlay infiniteLoop showThumbs={false} className={styles.slider}>
      {images.map((image, index) => (
        <div key={index} className={styles.imageContainer}>
          <img
            src={image.src}
            alt={image.alt}
            className={styles.image}
            onError={e => {
              e.target.style.display = 'none';
            }}
          />
          <div className={styles.placeholder}>Image Not Available</div>
          <p className="legend">{image.caption}</p>
        </div>
      ))}
    </Carousel>
  );
}

export default ImageSlider;
