import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

const AdminRedirect = () => {
  const navigate = useNavigate();

  useEffect(() => {
    // sessionStorage   
    const token = sessionStorage.getItem('github_access_token');
    const REDIRECT_URI = 'https://bearailab.github.io/callback';

    if (token) {
      //   /admin  
      navigate('/actual-admin'); //  admin  
    } else {
      //   GitHub OAuth  
      window.location.href = `https://github.com/login/oauth/authorize?client_id=Ov23liZ6tuy0yNyYfbNV&redirect_uri=${encodeURIComponent(REDIRECT_URI)}&scope=repo user`;
    }
  }, [navigate]);

  return <div>Redirecting...</div>;
};

export default AdminRedirect;
